import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent} from '../components/styles.js'

class ContactPage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>

              <p>
                CHECK ME OUT ON <StyledText onMouseOver={() => this.mouseOver('hover1')} onMouseOut={() => this.mouseOut('hover1')}>
                <a href='https://linkedin.com/jdnk'>
                LinkedIn
                </a>
                </StyledText> AND <StyledText onMouseOver={() => this.mouseOver('hover2')} onMouseOut={() => this.mouseOut('hover2')}>
                <a href='https://github.com/jdnk'>
                GitHub
                </a>
                </StyledText> OR CONTACT ME VIA <StyledText onMouseOver={() => this.mouseOver('hover3')} onMouseOut={() => this.mouseOut('hover3')}>
                <a href='mailto:jaden.kim@pomona.edu'>
                Email
                </a>
                </StyledText>.
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default ContactPage



